import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/introduction",
    name: "Introduction",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Introduction.vue")
  },
  {
    path: "/faq",
    name: "Faq",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Faq.vue")
  }
];

const router = new VueRouter({
  routes
});

router.afterEach((to,from,next) => {

  window.scrollTo(0,0);

});

export default router;
